.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    position: sticky;
    top: 0;
    z-index: 100;
    background-color:white;
}

.header_logo{
    height: 25px;
    object-fit: contain;
    margin-left: 20px;
}

.header_input{
    display: flex;
    align-items: center;
    width: 40%;
    border: 1px solid gray;
    
}

.header_icon{
    margin-right: 8px;
    
}

.header_inputbutton{
    width: 50px !important;
    border-color: #fafafa;
    border-left: 1px solid gray;
    color: grey;
    
    

}
.header_icons{
  display: flex;
  align-items: center;
}

.header_input>input{
    flex:1;
    /* border-collapse:collapse; */
    border:none ;
    padding: 10px;
}